'use client';

import { useState } from 'react';
import { DatePicker } from '@mantine/dates';
import { MantineProvider } from '@mantine/core';

export default function Date() {
  return (
    <MantineProvider>
      <DateComponent />
    </MantineProvider>
  );
}

function DateComponent() {
  const [value, setValue] = useState<string | null>(null);

  return (
    <DatePicker
      value={value}
      onChange={setValue}
    />
  );
}
